import React, { useState, useRef, useEffect } from "react";
import wrapper from "../../commons/WebComponentWrapper";
import { ProgressIndicator } from "carbon-components-react";

const dispatchedEvents = [];

/*
    Example: <srs-progressbar label="Progress" max="100" value="75"/>
*/
const SrsProgressBar = function (props, ref) {
    return (
        <ProgressIndicator
            value={props.value}
            max={props.max}
            aria-label={props.label}
        />
    );
}

const styles = `
    
`
const props = [{
    name: "label",
    defaultValue: "Label",
    type: "string"
}, {
    name: "max",
    defaultValue: 100,
    type: "number"
}, {
    name: "value",
    defaultValue: 10,
    type: "number"
}]

const slots = [];

const WrappedComponent = wrapper(React.forwardRef(SrsProgressBar), props);
export { WrappedComponent, styles, props, dispatchedEvents, slots };